:root {
  font-size: 62.5%;
}
* {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  text-align: center;
  color: black;
  font-size: 1.2rem;
}
.array-container {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  max-width: 100rem;
  margin: 0 auto;
}
.array-bar {
  display: inline-block;
  margin: 0 1px;
  width: 30px;
}

.bar-animation {
  animation-name: completed;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes completed {
  from {
    background-color: rgb(195, 255, 177);
  }
  to {
    background-color: rgba(94, 255, 45, 0.9);
  }
}

.navbar-brand {
  font-size: 1.6rem !important;
  color: #ffffff !important;
}

.btn {
  font-size: 1.4rem !important;
}
p {
  font-size: 1.6rem;
}
.slidecontainer {
  width: 100%;
}

.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 15px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #4caf50;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #4caf50;
  cursor: pointer;
}

.slider:disabled {
  opacity: 1;
  cursor: not-allowed;
}
